<template>
<div class="container">
  <div class="row margin"></div>
    <div class="row">
      <div class="input-field col s4">
        <a class="waves-effect waves-light btn" href="#/products/new">Novo</a>
      </div>
      <div class="input-field right col s4">
        <input type="text" id="autocomplete" class="autocomplete">
        <label for="autocomplete">Buscar</label>
      </div>
    </div>
  <table>
    <thead>
      <tr>
        <th>Cdigo de barras</th>
        <th>Descrio</th>
        <th>Valor</th>
        <th class="center-padding">Aes</th>
      </tr>
    </thead>
    <tbody>
      <tr v-for="sale in sales">
        <td>{{sale[1]}}</td>
        <td>{{sale[2]}}</td>
        <td>{{sale[3]}}</td>
        <td class="right">
          <a class="waves-effect waves-light btn-small green btn" @click="stock(sale[0])"><i class="material-icons">import_export</i></a>
          <a class="waves-effect waves-light btn-small blue btn" @click="details(sale[0])"><i class="material-icons">edit</i></a>
          <a class="waves-effect waves-light btn-small red btn" @click="del(sale[0])"><i class="material-icons">delete</i></a>
        </td>
      </tr>
    </tbody>
  </table>
</div>
</template>


<script>
import $ from 'jquery'
export default {
  name: 'sale-show',
  computed: {
    sales: function () {
      return this.$store.sale.state.sales
    }
  },
  mounted () {
    this.$store.sale.dispatch('getAll').then(() => {
      var teste = this.$store.sale.state.sales
      teste.forEach(element => {
        console.log(element)
      })
    })
    var teste = {
      'Apple': null,
      'Microsoft': null,
      'Google': 'https://placehold.it/250x250'
    }
    $(function () {
      $('input.autocomplete').autocomplete({
        data: teste,
        limit: 20
      })
    })
  },
  methods: {
    del: function (id) {
      this.$store.sale.dispatch('remove', id).then(() => {
      })
    },
    details: function (id) {
      this.$router.push('/sales/view/' + id)
    },
    inventoryMovement: function (id) {
      $('.modal').modal()
      $('#inventoryMovement').modal('open')
      $('.trigger-modal').modal()
    },
    saveInventoryMovement: function () {
      alert('123')
    },
    stock: function (id) {
      this.$router.push('/sales/details/' + id)
    }
  }
}
</script>

<style>
.btn-small {
    height: 24px;
    line-height: 24px;
    padding: 0 0.5rem;
}
.center-padding {
  text-align: right;
  padding: 2.5%;
}
</style>



