<template>
<div class="row ">
<div class="row"></div>
<div class="col s12">
        <div class="row z-depth-1">
        <div class="input-field col s3">
          <input id="last_name" placeholder="Cdigo de Barras" type="text" class="validate" @keyup.enter="addProduct" v-model="barcode" v-focus>
        </div>
        <div class="row col s9">
          <div class="input-field col s3 right">
            <div><span class="teal-text text-darken-4 ">Cliente Padro</span>
              <button class="btn cyan waves-effect btn-small waves-light right"><i class="material-icons">add</i></button>
            </div>
          </div>
        </div>
      </div>
</div>
<div class="row"></div>
<div class="col s12">
  <div class="col s9">
    <div class="row col s12 z-depth-1">
      <div class="col s12">
        <table>
          <thead>
            <tr>
              <th>Nome</th>
              <th>Valor</th>
              <th class="center-padding">Aes</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="(line,key) in products">
              <td>{{line.name}}</td>
              <td>{{line.value}}</td>
              <td class="right">
                <a class="waves-effect waves-light btn-small blue btn"><i class="material-icons" @click.prevent="discount(key)">edit</i></a>
                <a class="waves-effect waves-light btn-small red btn" @click.prevent="delProduct(key)"><i class="material-icons">delete</i></a>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
  <div class="center">
    <div class="col s3 right">
    <table class="bordered">
      <thead><tr></tr></thead>
      <tbody>
        <tr>
          <td class="red-text center left"><h5>Total: R$ </h5></td>
          <td class="red-text center right"><h5>{{total}}</h5></td>
        </tr>
        <tr>
          <td class="black-text center left"><h5>Desconto: R$ </h5></td>
          <td class="black-text center right"><h5>{{total}}</h5></td>
        </tr>
      </tbody>
    </table>
    <div class="row"></div>
    <button type="submit" class=" modal-action modal-close waves-effect btn col s12 btn-large" @click="payment">Pagamento</button>
  </div>
  </div>
  <div id="payment" class="modal">
    <form class="col s12"  @submit.prevent="savePayment">
      <div class="modal-content">
        <div class="row">
          <div class="col s1"></div>
          <div class="input-field col s4">
            <button type="submit" class=" modal-action modal-close waves-effect btn btn-large blue col s12"><i class="material-icons right">attach_money</i>Dinheiro</button>
          </div>
          <div class="col s2"></div>
          <div class="input-field col s4">
            <input type="number" class="validate">
            <label>Dinheiro</label>
          </div>
        </div>
        <div class="row">
          <div class="col s1"></div>
          <div class="input-field col s4">
            <button type="submit" class=" modal-action modal-close waves-effect btn btn-large blue col s12"><i class="material-icons right">credit_card</i>Carto</button>
          </div>
          <div class="col s2"></div>
          <div class="input-field col s4">
            <input type="number" class="validate">
            <label>Carto</label>
          </div>
        </div>
        <div class="row">
          <div class="col s1"></div>
          <div class="input-field col s4">
            <button type="submit" class=" modal-action modal-close waves-effect btn btn-large blue col s12"><i class="material-icons right">local_offer</i>Fiado</button>
          </div>
          <div class="col s2"></div>
          <div class="input-field col s4">
            <input type="number" class="validate">
            <label>Fiado</label>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="submit" class=" modal-action modal-close waves-effect btn">Salvar</button>
      </div>
    </form>
  </div>
</div>
</div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
    }
  },
  methods: {
    payment: function () {
      $('.modal').modal()
      $('#payment').modal('open')
      $('.trigger-modal').modal()
    },
    addProduct: function () {
      this.$store.pdv.dispatch('getOne', this.barcode)
      this.barcode = ''
    },
    delProduct: function (key) {
      this.$store.pdv.commit('removeOne', key)
    },
    discount: function (key) {
      let result = {
        key: key,
        data: 1
      }
      this.$store.pdv.commit('updateValue', result)
    }
  },
  computed: {
    products: function () {
      return this.$store.pdv.state.products
    },
    total: function () {
      let sum = 0
      this.$store.pdv.state.products.forEach(element => {
        sum += element.value
      })
      return sum
    }
  }
}
</script>

<style>
.btn-small {
    height: 24px;
    line-height: 24px;
    padding: 0 0.5rem;
}
.center-padding {
  text-align: right;
  padding: 2.5%;
}
</style>



